<template>
  <section>
    <div class="nav">
      <div @click="handleToogle(1)" :class="active===1?'nav-item active':'nav-item'">沪股通涨幅榜</div>
      <div @click="handleToogle(2)" :class="active===2?'nav-item active':'nav-item'">深股通涨幅榜</div>
      <!-- input输入查询框 -->
      <a-select
        v-if="active===1"
        showSearch
        :value="shContent"
        placeholder="股票名称或代码"
        style="width: 160px;padding-top: 10px;margin-right: 20px;float:right;"
        :defaultActiveFirstOption="false"
        :showArrow="false"
        :filterOption="false"
        @search="handleShSearch"
        @change="handleShChange"
        :notFoundContent="null"
      >
        <a-select-option v-for="d in shSearchData" :key="d.value">{{d.text}}</a-select-option>
      </a-select>
      <a-select
        v-if="active===2"
        showSearch
        :value="szContent"
        placeholder="股票名称或代码"
        style="width: 160px;padding-top: 10px;margin-right: 20px;float:right;"
        :defaultActiveFirstOption="false"
        :showArrow="false"
        :filterOption="false"
        @search="handleSzSearch"
        @change="handleSzChange"
        :notFoundContent="null"
      >
        <a-select-option v-for="d in szSearchData" :key="d.value">{{d.text}}</a-select-option>
      </a-select>
      <div class="clear"></div>
    </div>
    <riseRankSh v-if="active===1" :sh-content="shContent"></riseRankSh>
    <riseRankSz v-if="active===2" :sz-content="szContent"></riseRankSz>
  </section>
</template>

<script>
import {
  getShTodayChangeList, // 沪股通涨幅榜搜索列表
  getSzTodayChangeList // 深股通涨幅榜搜索列表
} from '@/api/index'
import riseRankSh from './riseRankSh'
import riseRankSz from './riseRankSz'
export default {
  name: 'riseRank',
  data() {
    return {
      shContent: undefined,
      szContent: undefined,
      shSearchData: [],
      szSearchData: [],
      active: 1
    }
  },
  components: {
    riseRankSh,
    riseRankSz
  },
  methods: {
    handleToogle(args) {
      this.active = args
      this.shContent = undefined
      this.szContent = undefined
    },
    handleShChange(value) {
      this.shContent = value
    },
    handleSzChange(value) {
      this.szContent = value
    },
    // 输入即时搜索
    handleShSearch(val) {
      this.shContent = val
      let data = {
        pageNum: 1,
        pageSize: 10,
        content: val
      }
      getShTodayChangeList(data).then(res => {
        console.log(res)
        if (res.code === 200) {
          let arr = []
          let obj = res.data.data
          for (let i = 0; i < obj.length; i++) {
            arr.push({ value: obj[i].id, text: obj[i].id + ' ' + obj[i].name })
          }
          this.shSearchData = arr
        }
      })
    },
    // 输入即时搜索
    handleSzSearch(val) {
      this.szContent = val
      let data = {
        pageNum: 1,
        pageSize: 10,
        content: val
      }
      getSzTodayChangeList(data).then(res => {
        console.log(res)
        if (res.code === 200) {
          let arr = []
          let obj = res.data.data
          for (let i = 0; i < obj.length; i++) {
            arr.push({ value: obj[i].id, text: obj[i].id + ' ' + obj[i].name })
          }
          this.szSearchData = arr
        }
      })
    }
  }
}
</script>
<style lang="scss">
.nav {
  height: 50px;
  line-height: 50px;
  color: #262626;
  .nav-item {
    height: 50px;
    display: inline-block;
    background-color: #e3efff;
    padding: 0 14px;
  }
  .nav-item.active {
    color: #2286ff;
    background-color: #fff;
    // border-top: 2px solid #2286ff;
  }
}
</style>
