<template>
  <section>
    <a-table
      :columns="columns"
      :rowKey="record => record.idx"
      :dataSource="tableData"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
    </a-table>
  </section>
</template>

<script>
import {
  getSzStockInfo // 深股通成交榜
} from '@/api/index'
const columns = [
  { align: 'center', title: '代码', dataIndex: 'code' },
  { align: 'center', title: '上市公司', dataIndex: 'name' },
  { align: 'center', title: '上市时间', dataIndex: 'date' },
  // { align: 'center', title: '总市值', dataIndex: '' },
  { align: 'center', title: '收盘价', dataIndex: 'price' },
  { align: 'center', title: '涨跌幅', dataIndex: 'quoteChange' },
  { align: 'center', title: '深股通静买额', dataIndex: 'theNetBuying' },
  { align: 'center', title: '深股通买入金额', dataIndex: 'buy' },
  { align: 'center', title: '深股通卖出金额', dataIndex: 'sell' },
  { align: 'center', title: '深股通成交额', dataIndex: 'volume' }
]
export default {
  name: 'szDealRank',
  props: [ 'szContent', 'szTime' ],
  data() {
    return {
      tableData: [],
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      loading: false,
      columns
    }
  },
  created() {
    this.getData()
  },
  methods: {
    handleTableChange(pagination, filters, sorter) {
      this.pagination = { ...pagination }
      this.getData()
    },
    getData() {
      let data = {
        time: this.szTime,
        content: this.szContent,
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.loading = true
      getSzStockInfo(data).then(res => {
        console.log('深股通成交榜')
        console.log(res)
        if (res.code === 200) {
          let obj = res.data.data
          for (let i in obj) {
            obj[i].idx = i
          }
          this.tableData = obj
          const pagination = { ...this.pagination }
          pagination.total = res.data.total
          this.pagination = pagination
        }
        this.loading = false
      })
    }
  },
  watch: {
    szContent(newVal, oldVal) {
      console.log(newVal, oldVal)
      this.getData()
    },
    szTime(newVal, oldVal) {
      this.getData()
    }
  }
}
</script>
