<template>
  <section>
    <a-table
      :columns="columns"
      :rowKey="record => record.idx"
      :dataSource="tableData"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
    </a-table>
  </section>
</template>

<script>
import {
  getShTodayChange // 沪股通涨幅榜
} from '@/api/index'
const columns = [
  { align: 'center', title: '代码', dataIndex: 'id' },
  { align: 'center', title: '上市公司', dataIndex: 'name' },
  { align: 'center', title: '上市时间', dataIndex: 'listingDate' },
  // { align: 'center', title: '总市值', dataIndex: '' },
  { align: 'center', title: '市盈率PE-TTM', dataIndex: 'peRate' },
  { align: 'center', title: '市净率PB', dataIndex: 'pbRate' },
  { align: 'center', title: '最新价', dataIndex: 'price' },
  { align: 'center', title: '涨跌幅', dataIndex: 'quoteChange' },
  { align: 'center', title: '涨跌额', dataIndex: 'riseAndFall' },
  { align: 'center', title: '成交量', dataIndex: 'volume' },
  { align: 'center', title: '成交额', dataIndex: 'turnover' },
  { align: 'center', title: '换手率', dataIndex: 'handTurnoverRate' }
]
export default {
  name: 'shDealRank',
  props: [ 'shContent' ],
  data() {
    return {
      tableData: [],
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      loading: false,
      columns
    }
  },
  created() {
    this.getData()
  },
  methods: {
    handleTableChange(pagination, filters, sorter) {
      this.pagination = { ...pagination }
      this.getData()
    },
    getData() {
      let data = {
        content: this.shContent,
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.loading = true
      getShTodayChange(data).then(res => {
        console.log('沪股通涨幅榜')
        console.log(res)
        if (res.code === 200) {
          let obj = res.data.data
          for (let i in obj) {
            obj[i].idx = i
          }
          this.tableData = obj
          const pagination = { ...this.pagination }
          pagination.total = res.data.total
          this.pagination = pagination
        }
        this.loading = false
      })
    }
  },
  watch: {
    shContent(newVal, oldVal) {
      console.log(newVal, oldVal)
      this.getData()
    }
  }
}
</script>
