<template>
  <section>
    <div class="nav">
      <div @click="handleToogle(1)" :class="active===1?'nav-item active':'nav-item'">沪股通成交榜</div>
      <div @click="handleToogle(2)" :class="active===2?'nav-item active':'nav-item'">深股通成交榜</div>
      <!-- input输入查询框 -->
      <div style="float:right;margin: 0 20px;" v-if="active===1">
        <a-select
          showSearch
          :value="shContent"
          placeholder="股票名称或代码"
          style="width: 160px;"
          :defaultActiveFirstOption="false"
          :showArrow="false"
          :filterOption="false"
          @search="handleShSearch"
          @change="handleShChange"
          :notFoundContent="null"
        >
          <a-select-option v-for="d in shSearchData" :key="d.value">{{d.text}}</a-select-option>
        </a-select>
      </div>
      <div style="float:right;" v-if="active===1">
        <a-datePicker style="width: 120px;" @change="shTimeChange" :value="moment(shTime, 'YYYY-MM-DD')" :allowClear=false></a-datePicker>
      </div>
      <div style="float:right;margin: 0 20px;" v-if="active===2">
        <a-select
          showSearch
          :value="szContent"
          placeholder="股票名称或代码"
          style="width: 160px;"
          :defaultActiveFirstOption="false"
          :showArrow="false"
          :filterOption="false"
          @search="handleSzSearch"
          @change="handleSzChange"
          :notFoundContent="null"
        >
          <a-select-option v-for="d in szSearchData" :key="d.value">{{d.text}}</a-select-option>
        </a-select>
      </div>
      <div style="float:right;" v-if="active===2">
        <a-datePicker style="width: 120px;" @change="szTimeChange" :value="moment(szTime, 'YYYY-MM-DD')" :allowClear=false></a-datePicker>
      </div>
      <div class="clear"></div>
    </div>
    <dealRankSh v-if="active===1" :sh-content="shContent" :sh-time="shTime"></dealRankSh>
    <dealRankSz v-if="active===2" :sz-content="szContent" :sz-time="szTime"></dealRankSz>
  </section>
</template>

<script>
import moment from 'moment'
import {
  getShStockList, // 沪股通成交榜搜索列表
  getSzStockList // 深股通成交榜搜索列表
} from '@/api/index'
import dealRankSh from './dealRankSh'
import dealRankSz from './dealRankSz'
export default {
  name: 'dealRank',
  data() {
    return {
      // shTime: moment().format('YYYY-MM-DD'),
      shTime: moment().subtract('days', 1).format('YYYY-MM-DD'),
      szTime: moment().subtract('days', 1).format('YYYY-MM-DD'),
      shContent: undefined,
      szContent: undefined,
      shSearchData: [],
      szSearchData: [],
      active: 1
    }
  },
  components: {
    dealRankSh,
    dealRankSz
  },
  created() {
  },
  methods: {
    moment,
    handleToogle(args) {
      this.active = args
      this.shContent = undefined
      this.szContent = undefined
    },
    shTimeChange(val, time) {
      console.log(val, time)
      this.shTime = time
    },
    szTimeChange(val, time) {
      console.log(val, time)
      this.szTime = time
    },
    handleShChange(value) {
      this.shContent = value
    },
    handleSzChange(value) {
      this.szContent = value
    },
    // 输入即时搜索
    handleShSearch(val) {
      this.shContent = val
      let data = {
        pageNum: 1,
        pageSize: 10,
        content: val
      }
      getShStockList(data).then(res => {
        console.log(res)
        if (res.code === 200) {
          let arr = []
          let obj = res.data.data
          for (let i = 0; i < obj.length; i++) {
            arr.push({ value: obj[i].code, text: obj[i].code + ' ' + obj[i].name })
          }
          this.shSearchData = arr
        }
      })
    },
    // 输入即时搜索
    handleSzSearch(val) {
      this.szContent = val
      let data = {
        pageNum: 1,
        pageSize: 10,
        content: val
      }
      getSzStockList(data).then(res => {
        console.log(res)
        if (res.code === 200) {
          let arr = []
          let obj = res.data.data
          for (let i = 0; i < obj.length; i++) {
            arr.push({ value: obj[i].code, text: obj[i].code + ' ' + obj[i].name })
          }
          this.szSearchData = arr
        }
      })
    }
  }
}
</script>
<style lang="scss">
.nav {
  height: 50px;
  line-height: 50px;
  color: #262626;
  .nav-item {
    height: 50px;
    display: inline-block;
    background-color: #e3efff;
    padding: 0 14px;
  }
  .nav-item.active {
    color: #2286ff;
    background-color: #fff;
    // border-top: 2px solid #2286ff;
  }
}
</style>
